@import url(https://fonts.googleapis.com/css2?family=Cabin:wght@400;500;700&display=swap);
html,body,.App,#root{height:100%;width:100%;margin:0;padding:0}body{background-color:#111032;color:#fffdfd;font-family:"Cabin", sans-serif;font-weight:400}.population-scatter-plot,.population-bar-chart{margin:0 auto;display:block;width:100%;max-height:100%;overflow:visible}.tick line{stroke:rgba(255,255,255,0.4);stroke-width:1}.tick text{fill:white;font-size:20px}.tick:first-of-type line{stroke-width:8;stroke-linecap:round;stroke:#04d99d}.country-dots{fill:rgba(4,217,157,0.8);stroke:rgba(255,255,255,0.7);stroke-width:8}.tooltip{background-color:rgba(17,16,50,0.7);padding:1rem;position:fixed;opacity:0;pointer-events:none}.tooltip.show{opacity:1}.tooltip h5,.tooltip p{margin-top:0;margin-bottom:0.2rem}.tooltip p.country-title{font-weight:500}.label{fill:white;font-size:32px}.label.title{font-size:40px;font-weight:700}.bars rect{fill:#04d99d;stroke:rgba(0,0,0,0.3);stroke-width:3}.dashboard{grid-template-columns:1fr 1fr;width:90%;max-width:1600px;margin:0 auto;grid-template-columns:repeat(24, 1fr);grid-template-rows:repeat(12, 1fr);height:80vh}@media only screen and (min-width: 1200px){.dashboard{display:grid}.scatterplot-container{grid-column:1 / span 16;grid-row:1 / span 12}.year-input{grid-column:span 8;grid-row:1 / span 5;display:grid;-webkit-align-content:center;align-content:center}.barchart-container{grid-column:17 / span 8;grid-row:span 6 /-1}}.barchart-container{grid-column:17 / span 8;grid-row:span 6 /-1;overflow:visible}h1.title{text-align:center;padding-top:4rem;margin-top:0}.population-bar-chart .tick:first-of-type line{stroke-width:1;stroke:rgba(255,255,255,0.4)}input[type="range"]{-webkit-appearance:none;width:100%;background:transparent}input[type="range"]::-webkit-slider-thumb{-webkit-appearance:none}input[type="range"]:focus{outline:rgba(255,255,255,0.4) 2px solid}input[type="range"]::-ms-track{width:100%;cursor:pointer;background:transparent;border-color:transparent;color:transparent}input[type="range"]::-webkit-slider-thumb{-webkit-appearance:none;border:none;height:36px;width:16px;border-radius:3px;background:#ffffff;cursor:pointer;margin-top:-14px}input[type="range"]::-moz-range-thumb{border:none;height:36px;width:16px;border-radius:3px;background:#ffffff;cursor:pointer}input[type="range"]::-ms-thumb{border:none;height:36px;width:16px;border-radius:3px;background:#ffffff;cursor:pointer}input[type="range"]::-webkit-slider-runnable-track{width:100%;height:8.4px;cursor:pointer;background:#f2c641;border-radius:1.3px;border:none}input[type="range"]:focus::-webkit-slider-runnable-track{background:#f2c641}input[type="range"]::-moz-range-track{width:100%;height:8.4px;cursor:pointer;background:#f2c641;border-radius:1.3px;border:none}input[type="range"]::-ms-track{width:100%;height:8.4px;cursor:pointer;background:transparent;border-color:transparent;border-width:16px 0;color:transparent}input[type="range"]::-ms-fill-lower{background:#f2c641;border:none;border-radius:2.6px}input[type="range"]:focus::-ms-fill-lower{background:#f2c641}input[type="range"]::-ms-fill-upper{background:#f2c641;border:0.2px solid #010101;border-radius:2.6px;box-shadow:1px 1px 1px #000000, 0px 0px 1px #0d0d0d}input[type="range"]:focus::-ms-fill-upper{background:#f2c641}.dot{transition:fill ease-in 200ms;cursor:pointer}.selected-country{fill:rgba(217,59,190,0.8)}

